// ÂèñÂæó MBTI ÂõõÁ¢ºÁµêÊûú
function getMBTIResult(mbti) {
  return (
    (mbti.E >= mbti.I ? 'E' : 'I') +
    (mbti.S >= mbti.N ? 'S' : 'N') +
    (mbti.T >= mbti.F ? 'T' : 'F') +
    (mbti.J >= mbti.P ? 'J' : 'P')
  )
}

